      .col-lg-8
        .cajon.color-secundario.p-4.mb-4(data-aos="fade-left")
          p.mb-0 La estadstica juega un papel de suma importancia en la investigacin cientfica, siendo soporte en el proceso de toma de decisiones, adems es til como direccionamiento y control en diferentes campos (como el de la salud).
        p(data-aos="fade-left") La salud de las personas es un bien de innumerable valor que debe ser administrado de la mejor manera; por lo tanto, los alcances de la gestin pblica requieren en gran parte de la calidad de la informacin respecto al objeto que es administrado y de los procedimientos utilizados para exponer dicha informacin.
        b(data-aos="fade-left") A nivel salud las estadsticas se agrupan en:
        .row.mt-3
          .col-lg
            ul.lista-ul--color
              li(data-aos="fade-left")
