          .col-lg
            ul.lista-ul--color
              li(data-aos="fade-left") 
                i.fas.fa-atom(style="color: #7E5EE3")
                | Vitales  
          .col-lg
            ul.lista-ul--color
              li(data-aos="fade-left") 
