    .row.mb-4
      p(data-aos="fade-left") Estas son utilizadas para describir aspectos como el grado de salud y el diagnstico de las patologas, precisar el alcance y restriccin de los programas de salud, as como el xito y fracaso de estos, entre otros.
      p(data-aos="fade-left") Como se mencion anteriormente, tener informacin clara y veraz es fundamental para lograr tomar decisiones certeras en la solucin de problemas, por lo cual se requiere de un procesamiento adecuado de los datos.
      p.mb-0(data-aos="fade-left") La estadstica es un campo del conocimiento que proporciona al investigador las herramientas para sacar conclusiones de cierta poblacin con base en la informacin proporcionada por la muestra, en conclusin, es una ciencia que estudia la recoleccin, el anlisis y la interpretacin de datos, ya sea como apoyo en la toma de decisiones o para aclarar situaciones regulares o irregulares de algn fenmeno o estudio aplicado (ver figura 1).

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .titulo-sexto.color-acento-botones.mb-4(data-aos="fade-right")
          h5 Figura 1.
          span Qu hace la estadstica?
        figure(data-aos="fade-up")
          img(src='@/assets/curso/temas/tema3/img2.png', alt='Figura 2. Da la respuesta al planteamiento Qu es la estadstica?<br>la respuesta es:<br>Es informacin buena, informacin suficiente y procesa informacin.')


    separador
    #t_3_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
       h2 3.1 Bioestadstica

    .row.mb-5
      p.mb-0(data-aos="fade-left") Es la aplicacin de la estadstica a informacin o datos provenientes de mediciones u observaciones tomadas en las reas de la biologa. Actualmente, estas tcnicas son parte fundamental del proceso de investigacin; son un argumento muy fuerte para convencer, si bien la estadstica no demuestra nada. El proceso de toma de decisiones en la empresa moderna tiene en la estadstica una de sus herramientas ms poderosas.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-7.order-lg-1.order-2
        p(data-aos="fade-right") La estadstica es aplicada para explicar cmo se comportan los fenmenos que son de inters para las personas, por lo cual es importante determinar en esta ciencia que se entiende por fenmenos.
        .cajon.color-primario.p-4.mb-3(data-aos="fade-right")
          p.mb-0 Un fenmeno es cualquier exteriorizacin de una actividad que se da por los humanos o por la naturaleza y puede ser percibida a nivel sensitivo o racional. 
        h5.mb-3(data-aos="fade-right") Ejemplos de fenmenos:
        ul.lista-ul--color
          li(data-aos="fade-right")
            i.fas.fa-atom(style="color: #7E5EE3")
            | El paso de estado lquido a gaseoso del agua.
          li(data-aos="fade-right")
            i.fas.fa-atom(style="color: #7E5EE3")
            | Las personas por da que se contagian de un virus.
          li(data-aos="fade-right")
            i.fas.fa-atom(style="color: #7E5EE3")
            | Los cambios climticos.
          li(data-aos="fade-right")
            i.fas.fa-atom(style="color: #7E5EE3")
            | La variacin de los costos de los productos de consumo humano.
          li(data-aos="fade-right")
            i.fas.fa-atom(style="color: #7E5EE3")
            | Secar el cabello mojado con el aire caliente del secador.
      .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema3/img3.svg', alt='Imgen decorativa')

    
    .row.justify-content-center.align-items-center.mb-3(data-aos="flip-down")
      .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-4
        .bloque-texto-g__img(
          :style="{'background-image': `url(${require('@/assets/curso/temas/tema3/img4.png')})`}" data-aos="fade-right"
        )
        .bloque-texto-g__texto.p-3(data-aos="fade-right")
          p.mb-0 Por diferentes razones, las personas desean obtener informacin acerca del comportamiento de diferentes fenmenos, por lo cual se realizan registros del estado de estos, en diversos momentos o espacios. Cuando hay viabilidad de extraer datos de un fenmeno, o tambin cuando no es posible, es necesario hacer uso de algn proceso que convierta dichos datos en informacin que permita la toma de decisiones relevantes, es decir, escoger la opcin ms pertinente dentro de un nmero considerable de posibilidades.

    .row.mb-4
      p.mb-0(data-aos="fade-left") Del registro y la medicin de un fenmeno se generan datos en gran volumen, por lo cual se deben procesar adecuadamente para lograr convertirlos en informacin aportante. Una manera de tratar los datos y extraer la informacin all contenida es usar las tcnicas estadsticas, este proceso se representa en la siguiente figura 2:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8
        .titulo-sexto.color-acento-botones.mb-4(data-aos="fade-right")
          h5 Figura 2.
          span Proceso para extraer informacin
        figure(data-aos="fade-left")
          img(src='@/assets/curso/temas/tema3/img5.svg', alt='Figura 3. Muestra el proceso para extraer informacin, esto es:<br>Los fenmenos, producen datos que al procesarlos de diferentes formas se convierten en:<br>Estadstica, o en Informacin y estos a su vez se utilizan para una toma de decisiones ms eficientes.')    

