    #t_3_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
       h2 3.2 Clasificacin de la estadstica

    .row.mb-5
      h4.text-center.mb-0 La estadstica se clasifica en:
    
    .bg1-tema3.mb-4(data-aos="flip-down")
