    separador
    #t_3_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
       h2 3.3 Conceptos bsicos de estadstica

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        figure(data-aos="fade-right")
          img(src='@/assets/curso/temas/tema3/img7.svg', alt='Imgen decorativa')
      .col-lg-7
        p(data-aos="fade-left") El estudio de la estadstica acoge una serie de significados, ideas y palabras con un determinado sentido en el da a da, pero que presentan una ligera diferencia o implicaciones completamente opuestas, estas son:
        .row(data-aos="fade-left")
          .col-lg-7
            .list.mb-3
              .list__item
                .list__item__icon
                  img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
              .list__txt
                h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Datos</span>

        p(data-aos="fade-left") Un dato es un registro o anotacin que se realiza del estado de un fenmeno en un momento especfico.
        .row.mt-5
          .col-lg-7
            .list.mb-3(data-aos="fade-right")
              .list__item
                .list__item__icon
                  img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
              .list__txt
                h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Elementos</span>
        p(data-aos="fade-left") El elemento se refiere a los entes que poseen una o varias caractersticas y su estado es de inters de registro. Ese registro es lo que integra los datos, los elementos pueden ser: 

    .bg1-tema3.mb-5(data-aos="flip-right")
      figure
        img(src='@/assets/curso/temas/tema3/img8.svg', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mb-5
      .row.mb-3
        .col-lg-7
          .list.mb-3(data-aos="flip-up")
            .list__item
              .list__item__icon
                img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
            .list__txt
              h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Poblacin</span>
      .col-lg-9
        p(data-aos="fade-right") Se habla de una poblacin cuando: 
        ul.lista-ul--color.ps-lg-4
          li(data-aos="fade-right")
            i.fas.fa-atom(style="color: #7E5EE3")
            | Se manifiesta una caracterstica igual en todos los elementos.
          li(data-aos="fade-right")
            i.fas.fa-atom(style="color: #7E5EE3")
            | Todos los elementos integran un acontecimiento que se est analizando y sobre el cual se intenta sacar conclusiones.
        h5 Cuando se habla de poblacin es importante tener claridad y hacer precisin con respecto a:
      .col-lg-3
        figure(data-aos="fade-left")
          img(src='@/assets/curso/temas/tema3/img9.svg', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in")
      TabsB.color-primario(style="background: #E6F7FE")
        .p-4.p-md-5(titulo="Definicin" :icono="require('@/assets/curso/temas/tema3/img10.svg')")
          .row.justify-content-center.align-items-center
            .col-lg-5.order-lg-1.order-2
              p Al momento de definir poblaciones, se debe hacer con claridad, a tal punto que no haya confusin de la pertenencia de un elemento a la poblacin.
              p Para hacer ms fcil la definicin de una poblacin, los componentes de esta se pueden organizar como se muestra en el diagrama.
              p #[strong Ejemplo:] en un estudio del grado salarial de los conductores de bus del sector Guayabal de la ciudad de Medelln, se podra definir la poblacin as:
              p Todos los conductores de bus del sector Guayabal de la ciudad de Medelln.
            .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema3/img11.svg', alt='Imagen de un diagrama que describe los componentes de la definicin de una poblacin, esto es:<br>Descripcin del elemento donde toda definicin debe iniciar por la plabra Todo(a)s luego se describe el elemento observado y luego se escribe una restriccin que abarque la palabra todo(a)s.')
        
        .p-4.p-md-5(titulo="Tamao" :icono="require('@/assets/curso/temas/tema3/img12.svg')")
          .row.justify-content-center.align-items-center
            .col-lg-10.order-lg-1.order-2
              p.text-center Son todos los elementos sin excepcin que conforman una poblacin. El tamao de esta se representa por con la letra N.
              p.text-center Ejemplo: para mostrar que una poblacin tiene 700 elementos se expresa as: N = 700
            .col-lg-10.order-lg-2.order-1.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema3/img13.svg', alt='Imgen decorativa')

        .p-4.p-md-5(titulo="Clases de poblacin" :icono="require('@/assets/curso/temas/tema3/img14.svg')")
          .row.justify-content-center.align-items-center
            .col-lg-10.order-lg-1.order-2
              p.text-center Las poblaciones se dividen en dos clases:
            .col-lg-11.order-lg-2.order-1.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema3/img15.svg', alt='Imagen que describe con ejemplos como deben quedar las deficiones para las poblaciones finitas y para las poblaciones infinitas.')

        .p-4.p-md-5(titulo="Caractersticas observables en una poblacin" :icono="require('@/assets/curso/temas/tema3/img16.svg')")
          .row.justify-content-center.align-items-center
            .col-lg-11.order-lg-1.order-2
              p.mb-0 Lo observable a los componentes de una poblacin son sus peculiaridades o la potencia con la que se ensea una magnitud.
              p Dichas peculiaridades observables en los elementos de una poblacin se clasifican en:
            .col-lg-10.order-lg-2.order-1.mb-lg-0.mb-4
              ImagenInfografica.color-acento-botones
                template(v-slot:imagen)
                  figure
                    img(src='@/assets/curso/temas/tema3/img17.svg', alt='Imagen que a travs de un organigrama describe la clasificacin de  las caractersticas de los elementos de una poblacin; estas se explican luego de la imagen.')

                .tarjeta.color-primario--borde.p-3(x="17%" y="12%" numero="+")
                  p #[b Constantes.] Son esas caractersticas que tienen una variacin nula o muy mnima entre un elemento y otro; por ejemplo, la talla de calzado de una persona en edad adulta observada en los ltimos 3 aos.
                .tarjeta.color-primario--borde.p-3(x="62%" y="12%" numero="+")
                  p #[b Variables.] Son esas caractersticas que tienen una variacin frecuente de valor en el momento de observar algunos o todos los elementos de la poblacin. Cabe aclarar que la estadstica solo se enfoca en el estudio de las caractersticas variables.
                .tarjeta.color-primario--borde.p-3(x="34%" y="49%" numero="+")
                  p #[b Variables cualitativas.] Estas describen solo por medio de palabras el estado de la caracterstica de los elementos. Se refieren a atributos, cualidades, comportamientos o preferencias de los elementos que estn siendo estudiados.
                  h5.mb-0 Ejemplos:
                  p.mb-0 - La textura de un tapete elaborado con lana: suave, spero, etc. 
                  p - El color de los ojos de una familia: cafs, negros, azules, verdes, etc.
                  p.mb-0 Como se puede observar, en los ejemplos, cada una de las variables se manifiesta por medio de dos o ms categoras: suave, spero, cafs, negros, azules, verdes.
                  p Los datos registrados cuando las variables son cualitativas o categricas pertenecen a la cantidad o proporcin de elementos que caben dentro de cada categora que toma la variable, por ejemplo: la cantidad de regentes de farmacia o de instructores, la cantidad de personas que prefieren el color azul, la proporcin de productos defectuosos.
                .tarjeta.color-primario--borde.p-3(x="24%" y="87%" numero="+")
                  p #[b Variables nominales.] Una variable cualitativa nominal presenta modalidades no numricas que no admiten un criterio de orden. Por ejemplo, no hay una manera comn para darle orden a los colores o al estado civil de las personas.
                .tarjeta.color-primario--borde.p-3(x="42%" y="87%" numero="+")
                  p #[b Variables ordinales.] Presenta modalidades no numricas, en las que existe un orden. Por ejemplo: las categoras como se puede calificar un periodo escolar, que se puede ordenar como excelente, bueno, mal.
                .tarjeta.color-primario--borde.p-3(x="81%" y="49%" numero="+")
                  p.mb-0 #[b Variable cuantitativa.] Son las que se describen por medio de nmeros, por ejemplo, la edad de los empleados de una empresa, los individuos que ingresan por da a una IPS, las ventas por da de un establecimiento farmacutico, la cantidad de productos que vende una distribuidora, etc. 
                  p Estas variables se clasifican en discretas o continuas:
                .tarjeta.color-primario--borde.p-3(x="61%" y="87%" numero="+")
                  p #[b Variables discretas.] Estas solamente toman valores enteros, como por ejemplo el nmero de productos que vende un establecimiento farmacutico, o el nmero de personas que ingresan a un supermercado.
                .tarjeta.color-primario--borde.p-3(x="80%" y="87%" numero="+")
                  p #[b Variables continas.] Por medio de estas se realizan mediciones de magnitudes fsicas o a caractersticas estimables en dinero. Tambin permiten valores fraccionarios o decimales, como por ejemplo el peso de una caja con productos, los saldos de las cuentas bancarias, o el tiempo que demora el recorrido de un barco.
                .tarjeta.color-primario--borde.p-3(x="98%" y="87%" numero="+")
                  p #[b Variables categricas.] Esta variable facilita el manejo de los datos y por ende ayuda a comprender mejor un fenmeno. Por ejemplo, como cuando las personas que miden menos de 1.50 metros se determinan de estatura baja, las personas que miden entre 1.50 metros y menos de 1.70 se determinan de estatura normal y los individuos que miden 1.70 metros o ms se determinan de estatura alta.

    .row.mb-4
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Censo</span>

    .row.mb-4
      p(data-aos="fade-left") Observacin y registro del estado de una caracterstica, examinado a todos los elementos de una poblacin.
      p.mb-0(data-aos="fade-left") Los censos son una tcnica muy aplicable, ya que los costos y el tiempo que demandan son muy altos. Por lo tanto, lo ms viable es tomar los datos reportados por el estado de la variable en forma aleatoria de elementos de la poblacin.

    .row.mb-4
      .col-lg-7
        .list(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Muestra</span>

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2
        p(data-aos="fade-right") Cuando de una poblacin total, se sustrae una porcin conformando un subgrupo, esto quiere decir que solo se le realiza la observacin y el registro de la caracterstica a este subgrupo. Se utiliza cuando la poblacin es infinita o extremadamente grande y por ende es casi imposible observar todos sus elementos.
        p(data-aos="fade-right") Las muestras deben ser representativas y para ello se necesita que haya una equivalencia entre las caractersticas de la poblacin escogida y las caractersticas de la poblacin total.
        p(data-aos="fade-right") El tamao de la muestra es la cantidad de elementos que conforman la muestra. Se representa con la letra n. Un ejemplo de esto es: para expresar que una muestra tiene 180 elementos, se representa as: n = 180.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema3/img18.svg', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        figure
          img(src='@/assets/curso/temas/tema3/img19.svg', alt='Imgen decorativa')
      .col-lg-8
        .row.mb-0
          .col-lg-7
            .list.mb-3(data-aos="fade-left")
              .list__item
                .list__item__icon
                  img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
              .list__txt
                h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Parmetro</span>
        p.mb-5(data-aos="fade-left") El resultado de una medida o clculo que se hace utilizando los datos relacionados con el valor que toma una variable cuando se observan todos los elementos de una poblacin, es decir, cuando se hace un censo. Por ejemplo, la edad promedio de los nios que cursan primer grado, este ao, en todas las escuelas oficiales de la ciudad. El parmetro siempre es un valor constante.
        .row.mb-0
          .col-lg-7
            .list.mb-3(data-aos="fade-left")
              .list__item
                .list__item__icon
                  img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
              .list__txt
                h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Estadstico o estadgrafo</span>
        p(data-aos="fade-left") Tiene el mismo modo de operacin que el parmetro, pero con la diferencia que en este solo se observan algunos de los elementos de una poblacin, o sea, una muestra. Por ejemplo, la edad promedio de los jvenes de octavo grado de algunos colegios privados de la Medelln-Antioqua escogidos al azar. El estadstico no es un valor constante, ya que vara de una muestra a otra. 

    .row.mb-2
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'> Estudios estadsticos</span>

    .row.justify-content-center.align-items-center.mb-5
      h5.text-center.mb-4(data-aos="fade-right") Los estudios estadsticos pueden ser:
      .col-lg-10
        .tarjeta--container.row
          .col-md.tarjeta.color-tarjeta1.p-5(data-aos="fade-right")
            .row.justify-content-center.mb-4
              .col-5
                figure
                  img(src='@/assets/curso/temas/tema3/img20.svg', alt='Imgen decorativa')
            h4.text-center Estudios estadsticos experimentales
            p.mb-0 Quien est investigando es el encargado de controlar o manipular una o diferentes variables con el propsito de establecer su comportamiento en una condicin definida.
          .col-md.tarjeta.color-tarjeta2.p-5(data-aos="fade-left")
            .row.justify-content-center.mb-4
              .col-5
                figure
                  img(src='@/assets/curso/temas/tema3/img21.svg', alt='Imgen decorativa')
            h4.text-center Estudios estadsticos de observacin
            p.mb-0 Quien est investigando se encarga de registrar el estado de la caracterstica variable que es de su inters sin producir influencia alguna sobre esta. La investigacin estadstica de observacin que es ms comnmente utilizada es la encuesta.      

    .row.mb-2
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Muestreo</span>
    
    .row.mb-5
      p.mb-0(data-aos="fade-left") Es el proceso mediante el cual se seleccionan los elementos de una poblacin. Hay una variedad de tcnicas que se pueden utilizar para realizar el muestreo, claro est que, segn el caso se debe escoger la que aplique. A continuacin, se mencionan algunas:

    .bg2-tema3.mb-4
      .row.justify-content-center.align-items-center
        .col-lg-5.col-sm-10(data-aos="zoom-in")
          figure
            img(src='@/assets/curso/temas/tema3/img22.svg', alt='Imagen que describe algunas de las tcnicas del muestreo que son entre otras:<br>Muestreo probabilstico, estratificado, aleatorio simple, sistemtico y conglomerdado.')      

    .row.mb-2
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Unidad de observacin</span>

    .row.justify-content-center.align-items-center.mb-5
      p(data-aos="fade-left") Conformada por cada uno de los elementos que constituyen la poblacin objeto de estudio y la cual se define con antelacin, destacando cada una de sus caractersticas puesto que en definitiva es a esta a quien se le aplicara la medicin. Uno o varios individuos pueden constituirla y nombrarse simple o compleja, respectivamente.
      b.mb-0(data-aos="fade-right") Ejemplos:
 
    .row.justify-content-center.mb-5
      .col-lg-4.col-sm-8.mb-lg-0.mb-4(data-aos="fade-right")
        .tarjeta-numerada.color-primario.p-5.h-100
          .tarjeta-numerada__numero
            .h2 1
          p.mb-0 En un estudio a nivel de hogares sobre el estado higinico de la familia, la unidad de observacin son los diferentes sitios que existen en la vivienda y en quienes se evala su estado higinico y la unidad de anlisis es la vivienda.
      .col-lg-4.col-sm-8.mb-lg-0.mb-4(data-aos="flip-up")
        .tarjeta-numerada.color-secundario.p-5.h-100(style="border-color: #CBBFF4 !important")
          .tarjeta-numerada__numero(style="background-color: #CBBFF4 !important")
            .h2 2
          p.mb-0 En un estudio donde se desea evaluar la situacin laboral de las personas que conforman las familias de una comunidad, mayores de 18 aos, la unidad de observacin son las personas mayores de 18 aos en quienes se analizar su situacin laboral y la unidad de anlisis es la familia.
      .col-lg-4.col-sm-8(data-aos="fade-left")
        .tarjeta-numerada.color-acento-contenido.p-5.h-100
          .tarjeta-numerada__numero
            .h2 3
          p.mb-0 En un estudio donde se pretende determinar la relacin entre el peso y la talla del recin nacido, la unidad de observacin y la unidad de anlisis coinciden, esto es, el recin nacido.

    .row.mb-4
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Unidad de medida</span>

    p.mb-5(data-aos="fade-left") Consiste en dar los valores de una variable en trminos de una medicin; por ejemplo, el investigador establecer el criterio si el peso se da en kilos, en libras o en gramos; si la talla se da en metros, en centmetros, en pies o en pulgadas; si el tiempo de permanencia de los pacientes en un hospital se da en horas, das o semanas.

    .row
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Formas del conocimiento</span>

    h5.text-center.mb-5(data-aos="fade-right") Existen dos formas del conocimiento:

    .bg3-tema3.mb-5(data-aos="zoom-in")
      .row.justify-content-center.align-items-center
        .col-lg-10
          ImagenInfografica.color-acento-botones
            template(v-slot:imagen)
              figure
                img(src='@/assets/curso/temas/tema3/img23.svg', alt='Imgen decorativa')

            .tarjeta.color-primario--borde.p-3(x="21%" y="19%" numero="+")
              p #[b Popular.] Es aquel que se logra por la experiencia que brinda la cotidianidad. Se caracteriza por ser superficial y no sistemtico.
            .tarjeta.color-primario--borde.p-3(x="77%" y="19%" numero="+")
              p #[b Cientfico.] Es aquel que es objetivo, real, exhaustivo, sistemtico, racional y trascendente. Utiliza el razonamiento y la intuicin con el fin de llegar a la verdad, basndose en una evaluacin objetiva. Las etapas del mtodo cientfico son:
            .tarjeta.color-primario--borde.p-3(x="54%" y="79%" numero="+")
              p #[b Observacin.] Observacin del fenmeno objeto de estudio por un determinado periodo de tiempo con la pretensin de lograr conocimiento de este.
            .tarjeta.color-primario--borde.p-3(x="76%" y="79%" numero="+")
              p #[b Formulacin.] Formulacin de hiptesis contrastables que estn encaminadas a dar una explicacin, a nivel hipottico, del comportamiento del fenmeno.
            .tarjeta.color-primario--borde.p-3(x="98%" y="79%" numero="+")
              p #[b Verificacin.] Verificacin del cumplimiento de las hiptesis mediante una nueva observacin del fenmeno con el propsito de comprobar las hiptesis planteadas. Si los experimentos funcionan, la hiptesis sale reforzada y puede convertirse en una teora o una ley natural.

    .row
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Etapas del conocimiento cientfico</span>

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        figure
          img(src='@/assets/curso/temas/tema3/img24.png', alt='Imgen decorativa')
      .col-lg-7
        p(data-aos="fade-left") El mtodo cientfico utiliza el razonamiento y la intuicin con el fin de llegar a la verdad, basndose en una evaluacin objetiva. Sus etapas son:
        ol.lista-ol--cuadro.fa-ul2
          li(data-aos="fade-left")
            .lista-ol--cuadro__vineta(style="background-color: #7E5EE3")
              span 1
            | Observacin del fenmeno objeto de estudio por un determinado periodo de tiempo con la pretensin de lograr conocimiento del mismo.  
          li(data-aos="fade-left")
            .lista-ol--cuadro__vineta(style="background-color: #7E5EE3")
              span 2
            | Formulacin de hiptesis contrastables que estn encaminadas a dar una explicacin, a nivel hipottico, del comportamiento del fenmeno.
          li(data-aos="fade-left")
            .lista-ol--cuadro__vineta(style="background-color: #7E5EE3")
              span 3
            | Verificacin del cumplimiento de las hiptesis mediante una nueva observacin del fenmeno con el propsito de comprobar las hiptesis planteadas. Si los experimentos funcionan, la hiptesis sale reforzada y puede convertirse en una teora o una ley natural.

    .row
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>La investigacin en el enfoque emprico analtico</span>

    p.mb-5(data-aos="fade-left") La estadstica es parte integrante del mtodo cientfico, desde este punto de vista deben darse los siguientes pasos en una investigacin:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10
        PasosA.color-primario.mb-5(tipo="n")
          .row.justify-content-center.align-items-center(data-aos="fade-left")
            .col-lg-8.order-lg-1.order-2
              h4 Cul es el problema?
              p Es necesario conocer la magnitud del problema y los vacos que existen, con la finalidad de cubrir una necesidad.
              p Una vez especificado el problema, deben definirse los objetivos generales y especficos de la investigacin y, en consecuencia, elaborar el marco terico, ya que es el sustento conceptual de la investigacin.
            .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema3/img25.svg', alt='Imgen decorativa')

          .row.justify-content-center.align-items-center.text-start(data-aos="fade-right")
            h4 Cmo se har la investigacin?
            p El diseo metodolgico implica definir el tipo de estudio, la poblacin y la muestra, la unidad de observacin y anlisis, la identificacin y operacionalizacin de variables, el plan de recoleccin, tabulacin y anlisis, los aspectos ticos del estudio, criterios de inclusin y exclusin, cronograma de actividades, presupuesto y bibliografa.
            p Las variables que se van a medir deben tener una definicin operativa, es decir una unidad de medida, naturaleza y nivel de medicin. (Se debe tener en cuenta la revisin bibliogrfica realizada en el marco terico).
            p Luego se definen las tcnicas e instrumentos donde est la informacin (fuentes primarias o secundarias) y qu tcnica se va a utilizar (observacin, encuesta o entrevista). Cualquiera de estas tcnicas requiere de un instrumento para registrar la informacin.
            p En el plan de anlisis o de tratamiento estadstico debe definirse si se proceder manualmente o por computador, qu medidas estadsticas se utilizarn para dar cumplimiento a los objetivos y cmo se presentarn los datos.

          .row.justify-content-center.align-items-center(data-aos="fade-left")
            .col-lg-8.order-lg-1.order-2
              h4 Es necesario hacer un muestreo?
              p Tericamente, en una investigacin, el estudio de toda la poblacin sera lo ideal, pues se llegara al conocimiento exacto o casi exacto del fenmeno de inters. Sin embargo, en lo cotidiano esto no es usual, por lo que se puede recurrir a una muestra en vez de la totalidad de la poblacin. 
            .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema3/img26.svg', alt='Imgen decorativa')

          .row.justify-content-center.align-items-center(data-aos="fade-right")
            .col-lg-4.mb-lg-0.mb-4
              figure
                img(src='@/assets/curso/temas/tema3/img27.svg', alt='Imgen decorativa')           
            .col-lg-8
              h4 Cmo se presentarn y analizarn los datos?
              p Comprende el resumen de la informacin de manera que sea fcil de entender. Se hace mediante cuadros y grficos ilustrativos que presentan los resultados resumidos de una forma pertinente.
    .row
      .col-lg-7
        .list.mb-3(data-aos="fade-right")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema1/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 2px solid #00BCD4'>Fases de una investigacin estadstica</span>

    p.mb-5(data-aos="fade-left") Ante una investigacin estadstica las fases varan mucho y dependen de diversas situaciones como los objetivos que se quieren alcanzar o el ambiente de realizacin del estudio. Aqu se presenta una de estas posibilidades:

    TabsA.color-secundario.mb-5(data-aos="zoom-in")
      .tarjeta.tarjeta-morada.p-4(titulo="Planeamiento")
        p Durante todo el proceso la fase ms relevante es la del planteamiento, ya que la calidad de los resultados obtenidos depende de la correcta elaboracin y desarrollo, de cada una de las etapas que componen dicha fase. Esta fase incluye:
        .row.justify-content-center
          .col-lg-11
            ImagenInfografica.color-acento-botones
              template(v-slot:imagen)
                figure
                  img(src='@/assets/curso/temas/tema3/img28.svg', alt='Imgen decorativa')

              .tarjeta.color-primario--borde.p-3(x="29%" y="24%" numero="+")
                p #[b Finalidad de la investigacin.] Resultado puntual que se va a lugar del estudio estadstico. Normalmente, se expresa en forma de una pregunta y a esta se le denomina: pregunta de investigacin.
              .tarjeta.color-primario--borde.p-3(x="63%" y="24%" numero="+")
                p #[b Naturaleza o clase de los datos.] Todos los datos, que se recolectan sobre un fenmeno, se pueden clasificar bsicamente en dos categoras: datos de naturaleza cualitativa y datos de naturaleza cuantitativa.
              .tarjeta.color-primario--borde.p-3(x="29%" y="76%" numero="+")
                p #[b Fuentes de la informacin.] Las fuentes de informacin de donde se pueden obtener los datos son fuentes de informacin primarias y secundarias. Las fuentes de informacin son primarias cuando los datos que se procesan se han obtenido directamente (realizadores del estudio) y son secundarias cuando los datos que se procesan se han obtenido a travs de otras personas o entidades, como, por ejemplo, el banco de la repblica, el DANE, la cmara de comercio o cualquier otro estudio ya realizado.
              .tarjeta.color-primario--borde.p-3(x="63%" y="76%" numero="+")
                p #[b Procedimiento para recolectar los datos.] Se refiere a las actividades que se van a ejecutar para recolectar estos datos. Estas actividades pueden ser: observacin directa, encuestas individuales a nivel personal o virtual, entrevistas personales, entrevistas grupales, la experimentacin.
              .tarjeta.color-primario--borde.p-3(x="98%" y="76%" numero="+")
                p #[b Diseo de instrumentos.] Los datos deben quedar registrados, por lo cual se requiere disear instrumentos para los registros. Por ejemplo, formatos para registro de observaciones, cuestionario o listado de asuntos que se llevaran cabo en una entrevista y la manera de registro de las respuestas de los entrevistados.
              .tarjeta.color-primario--borde.p-3(x="98%" y="24%" numero="+")
                p #[b Presupuesto.] Hace referencia la valoracin o clculo de los costos, de cada una de las etapas del estudio.

      .tarjeta.tarjeta-morada.p-4(titulo="Recoleccin de los datos")
        .row.justify-content-center.align-items-center
          .col-lg-7.order-lg-1.order-2
            p En esta fase se aplican los instrumentos diseados anteriormente, como cuestionarios, entrevistas o registros de las observaciones.
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema3/img29.svg', alt='Imgen decorativa')              

      .tarjeta.tarjeta-morada.p-4(titulo="Crtica y codificacin")
        .row.justify-content-center.align-items-center
          .col-lg-7.order-lg-1.order-2
            p Hace referencia a la valoracin de lo veraz, autntico y pertinente de los datos recogidos. 
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema3/img30.png', alt='Imgen decorativa')

      .tarjeta.tarjeta-morada.p-4(titulo="Tabulacin, grficas y medidas")
        .row.justify-content-center.align-items-center
          .col-lg-7.order-lg-1.order-2
            p Este hace referencia a cada uno de los procesos, por medio de los cuales, los datos son convertidos en tablas, cuadros resmenes, grficas representativas o nmeros, de tal manera que se pueda describir y entender el fenmeno que se est investigando.
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema3/img31.svg', alt='Imgen decorativa')

      .tarjeta.tarjeta-morada.p-4(titulo="Anlisis e interpretacin")
        p Anlisis e interpretacin es la fase en la cual se precisa el fin del estudio, ya sea, porque se d respuesta a la pregunta de investigacin, o porque se describa el fenmeno en investigacin o porque se saque alguna conclusin sobre l.
        h5 Este anlisis tiene 2 clases de alcance:
        ul.lista-ul--color.ps-lg-4
          li
            i.fas.fa-atom(style="color: #7E5EE3")
            | Anlisis e interpretacin descriptivos: tanto el anlisis como la interpretacin se cien solamente a los datos recogidos.
          li
            i.fas.fa-atom(style="color: #7E5EE3")
            | Anlisis e interpretacin inferencial: el anlisis y la interpretacin se expande a todos los elementos de la poblacin o cuando se estiman comportamientos futuros del fenmeno en investigacin.

    separador
    #t_3_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
       h2 3.4 Presentacin de la informacin

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2
        p(data-aos="fade-right") La informacin que proporcionan las variables debe presentarse de una manera adecuada. En el campo estadstico existen dos formas bsicas de presentacin de la informacin, siendo la primera las tablas o cuadros estadsticos y la segunda, la representacin grfica del fenmeno.
        p(data-aos="fade-right") Una tabla o cuadro estadstico es una representacin en forma ordenada de las variaciones de un fenmeno, clasificadas bajo una o ms variables; si el fenmeno se clasifica bajo una sola variable, recibe el nombre de cuadro o tabla simple y si se clasifica bajo dos o ms variables recibe el nombre de cuadro o tabla compuesto.
        p.mb-0(data-aos="fade-right") La representacin ms usual del conjunto de datos que constituyen la muestra es la tabla de frecuencias; esta es un cuadro con ttulos y leyendas explicativas en el cual aparecen los datos en forma organizada, incluyendo totales, frecuencias y porcentajes.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema3/img32.png', alt='Imgen decorativa')

    .row.justify-content-center.mb-5
      .cajon.color-secundario.p-4.mb-4(data-aos="flip-right")
        p.mb-0 Las tablas permiten organizar y realizar clculos sobre datos estadsticos de tamao considerable, adems hacen que el trabajo sea mucho ms fcil. Ordenan la informacin recogida de una muestra en la investigacin de una sola variable. Cuando se tiene una cantidad considerable de datos, se deben repartir en categoras dentro de una tabla para as facilitar el anlisis.  
      p.mb-0(data-aos="fade-left") La organizacin de los datos permite visibilizar las caractersticas de un acontecimiento o establecer el patrn de comportamiento de ese acontecimiento. En la estadstica existe una metodologa denominada tabla de distribucin de frecuencias, la cual consiste en formar conjuntos de categoras con los datos recolectados y elaborar as la tabla. Es importante tener presente:

    TabsB.color-primario.mb-5(style="background: #E6F7FE" data-aos="zoom-in")
      .p-4.p-md-5(titulo="Concepto de clase o categora" :icono="require('@/assets/curso/temas/tema3/img33.svg')")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            p En trminos generales, una categora es una agrupacin de elementos que poseen una o varias caractersticas iguales. Ejemplo, los nios del equipo de baloncesto.
            p Un conjunto de categoras se considera como una agrupacin de clases estadsticas siempre y cuando todas las clases, del conjunto, tengan, sincronizadas, estas tres caractersticas:
            ul.lista-ul--color.ps-lg-3.fa-ul2
              li
                i.fas.fa-atom
                p.mb-0 <b>Amplitud constante:</b> la amplitud de todas las categoras pertenecientes a un conjunto de categoras (en las cuales se agrupa un determinado grupo de datos) debe ser la misma para todo el conjunto.
              li
                i.fas.fa-atom
                p.mb-0 <b>Conjunto de clases mutuamente excluyentes:</b> hace referencia a que, de un grupo de datos en investigacin, cualquiera de los datos, debe pertenecer en sentido nico a una sola categora.
              li
                i.fas.fa-atom
                p.mb-0 <b>Conjunto de clases exhaustivas:</b> cuando un conjunto de categoras permite la adherencia a este de todos los datos de una muestra.
          .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema3/img34.svg', alt='Imgen decorativa')

      .p-4.p-md-5(titulo="Clase o categora estadstica" :icono="require('@/assets/curso/temas/tema3/img35.svg')")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            p En estadstica se denomina clase, al conjunto de datos que hacen parte de un intervalo llamado valores. Por ejemplo, para datos correspondientes a ingresos de personas podemos crear una clase de las personas que tienen ingresos entre $500.000 y $800.000. Toda clase estadstica tiene, por lo tanto, un lmite inferior y un lmite superior. En este caso $500.000 y $800.000 respectivamente.
          .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema3/img36.png', alt='Imgen decorativa')

      .p-4.p-md-5(titulo="Amplitud de clase o intervalo" :icono="require('@/assets/curso/temas/tema3/img37.svg')")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            p Es la diferencia que hay entre los lmites superior e inferior de un intervalo de clase. Retomando el ejemplo anterior la amplitud de la clase de ingresos es de $300.000. Es decir, que para calcular la amplitud de clase se resta del lmite superior de la clase el lmite inferior.
            p Para expresar estas ideas en smbolos, se llama A a la amplitud de la clase, LS al lmite superior de la clase y LI al lmite inferior de la clase, expresando aritmticamente la amplitud de la clase as: 
            i.mb-3 =
            p Para el ejemplo:
            i.mb-3 = =$800.000$500.000=$300.000
          .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema3/img38.png', alt='Imgen decorativa')

      .p-4.p-md-5(titulo="Antes de empezar" :icono="require('@/assets/curso/temas/tema3/img39.svg')")
        .row.justify-content-center.align-items-center
          .col-lg-6.order-lg-1.order-2
            p Para describir un acontecimiento representado por un conjunto de datos, la estadstica establece que se deben agrupar los datos en un conjunto de clases o categoras y con este conjunto construir la tabla de frecuencias o distribucin de frecuencias.
            p Para realizar este proceso se deben resolver, en primera instancia, las siguientes preguntas:
            ul.lista-ul--color.ps-lg-4.fa-ul2
              li
                i.fas.fa-atom
                | Cul es el tipo de la variable?
              li
