              p.mb-0 Las clases construidas de esta manera se llaman <b>clases cerradas,</b> porque en cada clase se contabilizan todos los datos, incluidos entre los dos lmites de la clase. Sin embargo, estas clases, como se puede observar, son de amplitudes constantes, mutuamente excluyentes y exhaustivas.

