    p.mb-5(data-aos="fade-right") Cuando el intervalo de valores que toma la variable es reducido y la variable es discreta, es ms prctico agrupar los datos en clases de amplitud cero, como se muestra en el siguiente caso. Aqu X simboliza los valores que toma la variable que son al mismo tiempo las clases estadsticas. Estas clases cumplen con las tres caractersticas de una clase estadstica: son de amplitud constante, son mutuamente excluyentes y son exhaustivas, y algunos ejemplos son:

    .row.justify-content-center.align-items-center.mb-5
      .tarjeta.color-tarjeta3.p-5(data-aos="zoom-in")
        SlyderA(tipo="b")
          .row.justify-content-center.align-items-center
