              figure
                img(src='@/assets/curso/temas/tema3/img57.svg', alt='se registra el nmero de errores por factura en la demostracin sobre 60 facturas.')

          .row.justify-content-center.align-items-center
            p.text-center En una encuesta de presupuesto familiar, se ha obtenido la siguiente informacin respecto al n de hijos en 21 familias: Variable x = n de hijos
            .col-lg-10.mb-3
              figure
                img(src='@/assets/curso/temas/tema3/img58.svg', alt='se presenta la informacin correspondiente a nmero de hijos entre 21 familias.')
            .col-lg-3.mb-3
              p La variable x toma valores entre 0 y 4, es decir, existen en este grupo 5 categoras o clases. Contando el nmero de familias en cada categora se forma la tabla.
            .col-lg-7.mb-3
              figure
                img(src='@/assets/curso/temas/tema3/img59.png', alt='Se muestra la estructura de las familias atendiendo que se dividen en 5 categoras.')

          .row.justify-content-center.align-items-center
            b.text-center.mb-5 Componentes de la tabla de frecuencia:
            ul.lista-ul--color.fa-ul2
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 #[b n =] tamao de la muestra.
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 #[b Xi =] valor de la variable en el individuo i
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 #[b fi = frecuencia absoluta.] N de veces que se repite la variable en la categora i
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 #[b Fi = frecuencia absoluta acumulada.] Indica el n de individuos hasta la categora i (Suma de las fi)
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 #[b hi = frecuencia relativa.] Porcentaje de la categora respecto del total, se obtiene dividiendo la frecuencia de la clase por el tamao de la muestra. (fi/n)
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 #[b Hi = frecuencia relativa acumulada.] Porcentaje acumulado (Suma de las hi)

          .row.justify-content-center.align-items-center
            b.text-center.mb-5 Detalles importantes en la tabla de frecuencias
            ul.lista-ul--color.fa-ul2
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 n es la suma de la columna fi, es decir, siempre debe dar como resultado el tamao de la muestra.
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 En la columna de frecuencia absoluta acumulada se va sumando los valores de la columna fi, por lo tanto, el ltimo valor debe ser igual a n.
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 La columna frecuencia relativa (hi) representa en % de familias en cada categora. Por ejemplo, en las categoras con 3 hijos a un 28.5% de familias. Esta columna debe sumar 1.
              li
                i.fas.fa-atom(style="color: #29A2D9")
                p.mb-0 La Hi acumula los valores de la frecuencia relativa, por lo tanto, el ltimo valor debe ser 1. Ejemplo H4: el 90.4% de las familias encuestadas tienen a los ms 3 hijos.


