      .col-lg-7
        p(data-aos="fade-left") Su construccin se realiza sobre el sistema de ejes cartesianos. Por medio de este se representan datos no numricos y que no requieren de un orden (nominal) o datos no numricos, pero que s requieren de orden (ordinal).
        p(data-aos="fade-left") Cada categora requiere de una lnea vertical en que la altura es la frecuencia absoluta de la categora. El ancho de la barra es a preferencia de quien realice el grfico.
        p(data-aos="fade-left") Otra razn para ser utilizado es cuando la variable en investigacin es numrica discreta. 

