      .col-lg-7
        p(data-aos="fade-left") El histograma se construye sobre el sistema de coordenadas cartesianas. Se utiliza cuando la variable en estudio es continua o est agrupada en una tabla de frecuencia con intervalos en cada categora.
        p(data-aos="fade-left") En el eje X se identifica la variable en estudio y en el eje Y se grfica la frecuencia absoluta o la frecuencia relativa. Consiste en una serie de rectngulos en donde su altura depende del valor de cada frecuencia.
        p(data-aos="fade-left") Cada categora de la variable se representa por una barra. El ancho de cada barra depende de la amplitud del intervalo.

    .row.mb-5(data-aos="fade-right")
      b.text.center.mb-0 Los polgonos de frecuencia hacen referencia a:

