              p El polgono de frecuencias se realiza partiendo del histograma unificando las marcas de clase proyectadas sobre el lado superior de los rectngulos y adjuntando, para el cierre de la figura, dos clases adicionales, una, por encima del lmite superior de la clase ms alta y la otra, por debajo del lmite inferior de la clase ms baja.
              p Para construir el polgono de frecuencias se necesita la introduccin del concepto de Marca de Clase.
              p La marca de clase es el punto medio de una clase. Se calcula sumando los lmites de cada clase y dividiendo este total por 2. El smbolo que normalmente es utilizado para representar la marca de clase es Xi. Estas tambin son utilizadas, cuando se necesita representar todos los valores de una clase por un solo nmero.

          .row.justify-content-center
            .col-lg-7.order-lg-1.order-2
