            p En el momento en el que una persona requiere tomar una decisin acerca de una puntual situacin, o necesita tener una visin rpida tanto del comportamiento como de las caractersticas de un acontecimiento en estudio, los histogramas y los polgonos de frecuencias son la mejor opcin, ya que favorecen esa toma de decisiones.
